import React from 'react';
import styled, { css } from 'styled-components';
import PropTypes from 'prop-types';
import Theme from '../../theme';

const Box = styled.div`
  display: flex;
  height: 100%;
  position: relative;
  border-radius: 3px;
  background-color: ${props =>
    props.disabled ? `${Theme.colours.grey_f5}` : `${Theme.colours.white}`};
  color: ${props =>
    props.disabled ? `${Theme.colours.grey_c6}` : `${Theme.colours.black}`};
  cursor: ${props => (props.disabled ? 'not-allowed' : 'pointer')};
  ${props =>
    props.active &&
    props.showTick &&
    css`
      ::after {
        content: '';
        width: 0;
        height: 0;
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 20px solid ${Theme.colours.linkblue};
        position: absolute;
        top: -3.5px;
        right: -13px;
        transform: rotate(45deg);
      }
    `};
  ${props =>
    props.active
      ? css`
          border: 2px solid ${Theme.colours.linkblue};
          overflow: hidden;
        `
      : css`
          border: 1px solid ${Theme.colours.grey_d6};
          padding: 1px;
        `};

  :hover {
    ${props => {
      let styledBorder = '';
      if (!props.active)
        styledBorder = `border: 1px solid ${Theme.colours.linkblue_l2};`;
      if (props.presentational)
        styledBorder = `
          border: 1px solid ${Theme.colours.grey_d6}; 
          cursor: unset;
        `;
      if (props.disabled)
        styledBorder = `border: 1px solid ${Theme.colours.grey_d6};`;

      return styledBorder;
    }};
  }
`;

const Tick = styled.span`
  position: absolute;
  display: inline-block;
  top: -1px;
  right: -1px;
  z-index: 1;
`;

const SelectBox = ({
  active,
  showTick,
  disabled,
  className,
  onClick,
  children,
  presentational
}) => (
  <Box
    onClick={onClick}
    className={className}
    role="button"
    active={active}
    showTick={showTick}
    disabled={disabled}
    presentational={presentational}
  >
    {active && showTick && <Tick />}
    {children}
  </Box>
);

SelectBox.propTypes = {
  /** Class name pass to the top parent component */
  className: PropTypes.string,
  /** The component is selected or not */
  active: PropTypes.bool,
  /** Sets the component to show tick when active */
  showTick: PropTypes.bool,
  /** The component is disabled or not */
  disabled: PropTypes.bool,
  /** The component is presentational or not */
  presentational: PropTypes.bool,
  /** On click callback when click on the component */
  onClick: PropTypes.func
};

export default SelectBox;
