import PropTypes from 'prop-types';
import styled, { keyframes } from 'styled-components';
import preloader from '../../assets/images/red-gradient-loader.png';
import { remCalc } from '@lux/helpers';

const spin = keyframes`
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
`;

const Spinner = styled.div`
  width: ${remCalc(48)};
  height: ${remCalc(48)};
  display: block;

  ::before {
    animation: ${spin} 1s linear infinite;
    width: 100%;
    height: 100%;
    background-image: url(${preloader});
    background-repeat: no-repeat;
    background-size: 100%;
    display: block;
    content: ' ';
  }
`;

Spinner.propTypes = {
  /**
   * @deprecated This prop has been removed. Please kindly make the necessary changes using a parent component. This message will be removed in the next release of @lux/components
   */
  show: PropTypes.bool
};

/** @component */
export default Spinner;
