import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Row from '../Row';
import SC from './styles';

class Pagination extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //Total number of page sets (total number of pages/number of pages to be displayed)
      //Eg. If numOfPagesToDisplay = 5, totalPages = 12, then totalPageSets = 12/5 = 3 (rounded up)
      totalPageSets: 0,
      //Current idx of page sets (E.g for pages 1-5, currentPageSet is 0) - zero indexed
      currentPageSet: 0,
      //Page numbers to be displayed. E.g., In the first set it is [1, 2, 3, 4, 5]
      pageNumbersToDisplay: [],
      currentPage: this.props.currentPage
    };
  }

  handleClickNext = () => {
    let currentPageSet = this.state.currentPageSet;
    currentPageSet = currentPageSet + 1;
    this.setPaginationArray(currentPageSet);
  };

  handleClickPrev = () => {
    let currentPageSet = this.state.currentPageSet;
    currentPageSet = currentPageSet - 1;
    this.setPaginationArray(currentPageSet);
  };

  handleClickFirst = () => {
    this.setPaginationArray(0);
  };

  handleClickLast = () => {
    let currentPageSet = this.state.totalPageSets - 1;
    this.setPaginationArray(currentPageSet);
  };

  handlePageChange(pageNumber) {
    this.setState({ currentPage: pageNumber });
    //If numOfPagesToDisplay is 0, pagination should still work, hence trigger onPageChange with currentPageSet + 1
    this.props.onPageChange(
      pageNumber ? pageNumber : this.state.currentPageSet + 1
    );
  }

  /**
   * Create an array of page numbers to be displayed
   * @param {number} numOfPagesToDisplay Number of pages to be displayed in each set
   * @param {number} currentPageSet Current idx of page sets (E.g for pages 1-5, currentPageSet is 0)
   * @param {number} totalPages Total number of pages available
   * @returns {number[]}
   */
  createPaginationArray(numOfPagesToDisplay, currentPageSet, totalPages) {
    //E.g., If currentPageSet = 1 (second set), numOfPagesToDisplay = 5, this will return an array [6, 7, 8, 9, 10]
    //Filter out numbers which are greater than total number of pages
    return [...Array(numOfPagesToDisplay)]
      .map((_, i) => currentPageSet * numOfPagesToDisplay + i + 1)
      .filter(x => x <= totalPages);
  }

  /**
   * Sets new state for pageNumbersToDisplay and currentPageSet and triggers callback
   * @param {number} currentPageSet Current idx of page sets (E.g for pages 1-5, currentPageSet is 0)
   */
  setPaginationArray(currentPageSet) {
    let numOfPagesToDisplay = this.props.numOfPagesToDisplay;
    let totalPages = this.props.totalPages;
    let pageNumbersToDisplay = this.createPaginationArray(
      numOfPagesToDisplay,
      currentPageSet,
      totalPages
    );

    this.setState({ pageNumbersToDisplay, currentPageSet });

    this.handlePageChange(pageNumbersToDisplay[0]);
  }

  componentDidMount() {
    let currentPageSet = this.state.currentPageSet;
    let totalPages = this.props.totalPages;
    let numOfPagesToDisplay = this.props.numOfPagesToDisplay;
    //Number of sets of pages (1-5, 6-10 is 2 sets)
    //Handle numOfPagesToDisplay = 0
    let totalPageSets =
      numOfPagesToDisplay === 0
        ? totalPages
        : Math.ceil(totalPages / numOfPagesToDisplay);
    let pageNumbersToDisplay = this.createPaginationArray(
      numOfPagesToDisplay,
      currentPageSet,
      totalPages
    );
    this.setState({ totalPageSets, pageNumbersToDisplay });
  }

  render() {
    const {
      PaginationContainer,
      List,
      ListItem,
      Anchor,
      AnchorCurrent,
      AnchorDisabled
    } = SC;

    return (
      this.props.totalPages > 1 && (
        <Row center="sm">
          <PaginationContainer>
            <List className="pagination" isInline={true}>
              <ListItem>
                {this.state.currentPageSet === 0 ? (
                  <AnchorDisabled>&laquo;</AnchorDisabled>
                ) : (
                  <Anchor onClick={this.handleClickFirst}>&laquo;</Anchor>
                )}
              </ListItem>
              <ListItem>
                {this.state.currentPageSet === 0 ? (
                  <AnchorDisabled>Prev</AnchorDisabled>
                ) : (
                  <Anchor onClick={this.handleClickPrev}>Prev</Anchor>
                )}
              </ListItem>
              {this.state.pageNumbersToDisplay.map(page => (
                <ListItem key={page}>
                  {this.state.currentPage === page ? (
                    <AnchorCurrent>{page}</AnchorCurrent>
                  ) : (
                    <Anchor onClick={() => this.handlePageChange(page)}>
                      {page}
                    </Anchor>
                  )}
                </ListItem>
              ))}
              <ListItem>
                {this.state.currentPageSet === this.state.totalPageSets - 1 ? (
                  <AnchorDisabled>Next</AnchorDisabled>
                ) : (
                  <Anchor onClick={this.handleClickNext}>Next</Anchor>
                )}
              </ListItem>
              <ListItem>
                {this.state.currentPageSet === this.state.totalPageSets - 1 ? (
                  <AnchorDisabled>&raquo;</AnchorDisabled>
                ) : (
                  <Anchor onClick={this.handleClickLast}>&raquo;</Anchor>
                )}
              </ListItem>
            </List>
          </PaginationContainer>
        </Row>
      )
    );
  }
}

Pagination.defaultProps = {
  currentPage: 1,
  numOfPagesToDisplay: 5,
  totalPages: 1,
  onPageChange: function() {}
};

Pagination.propTypes = {
  /** Current Page*/
  currentPage: PropTypes.number,
  /** Number of pages to display in one set*/
  numOfPagesToDisplay: PropTypes.number,
  /** Total number of pages*/
  totalPages: PropTypes.number,
  /** Event to be triggered on page change*/
  onPageChange: function() {}
};

export default Pagination;
