import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { noop } from '../../helpers';

import SC from './styles';
import IconArrowUp from '../../assets/svgs/arrow-up.svg';
import IconArrowDown from '../../assets/svgs/arrow-down.svg';

class DropDown extends Component {
  constructor() {
    super();

    this.state = {
      isOpen: false,
      highlightedItem: null,
      selectedIndex: null
    };

    this.windowClick = this.windowClick.bind(this);
    this.openDrawer = this.openDrawer.bind(this);
    this.closeDrawer = this.closeDrawer.bind(this);
    this.toggleDrawer = this.toggleDrawer.bind(this);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.windowClick);
  }

  componentWillMount() {
    this.setState({ selectedIndex: this.props.selectedIndex });
  }

  windowClick(e) {
    if (!this.refDropDownTrigger.contains(e.target) && this.state.isOpen) {
      this.closeDrawer();
    }
  }

  selectKeyPress(e) {
    switch (e.which) {
      case 9: // tab
        if (this.state.isOpen) {
          this.selectItem(this.state.highlightedItem);
        }
        break;

      case 13: // enter
        if (this.state.isOpen) {
          this.selectItem(this.state.highlightedItem);
        } else {
          this.openDrawer();
        }
        break;

      case 27: // escape
        if (this.state.isOpen) {
          this.closeDrawer();
        }
        break;

      case 32: // spacebar
        this.toggleDrawer();
        break;

      case 38: // up
        if (this.state.isOpen) {
          this.highlightItem(-1);
        } else {
          this.openDrawer();
        }
        break;

      case 40: // down
        if (this.state.isOpen) {
          this.highlightItem(1);
        } else {
          this.openDrawer();
        }
        break;
    }
  }

  mouseEnterItem(index) {
    const item = this.props.items[index];
    if (!item || item.disabled) return;
    this.setState({ highlightedItem: index });
  }

  highlightItem(change) {
    let currentSelected = this.state.highlightedItem || 0;
    let newValue = currentSelected + change;
    const { items } = this.props;

    for (let i = 0; i < this.props.items.length; i++) {
      if (newValue < 0) {
        newValue = this.props.items.length - 1;
      } else if (newValue === this.props.items.length) {
        newValue = 0;
      }
      const item = items[newValue],
        { isHeader, disabled } = item;

      if (isHeader || disabled) {
        newValue += change;
      } else {
        break;
      }
    }

    this.setState({ highlightedItem: newValue });
  }

  selectItem(index) {
    const item = this.props.items[index];

    if (item && !item.disabled) {
      this.setState({ selectedIndex: index, highlightedItem: null });
      this.props.onChange(this.props.items[index]);
    }
    this.closeDrawer();
  }

  toggleDrawer() {
    if (this.props.disabled) return;
    if (this.state.isOpen) this.closeDrawer();
    else this.openDrawer();
  }

  openDrawer() {
    if (this.props.disabled) return;
    this.setState({ isOpen: true });
    document.addEventListener('click', this.windowClick);
  }

  closeDrawer() {
    this.setState({ isOpen: false });
    document.removeEventListener('click', this.windowClick);
  }

  render() {
    const { disabled } = this.props;
    return (
      <SC.DropDownWrapper isOpen={this.state.isOpen}>
        <SC.DropDownTrigger
          onClick={this.toggleDrawer.bind(this)}
          isOpen={this.state.isOpen}
          innerRef={node => {
            this.refDropDownTrigger = node;
          }}
        >
          {this.state.selectedIndex !== null ? (
            <SC.SelectedValue>
              {this.props.items[this.state.selectedIndex].text}
            </SC.SelectedValue>
          ) : (
            <SC.Placeholder>{this.props.placeholder}</SC.Placeholder>
          )}
          <SC.DropDownArrow>
            {this.state.isOpen ? (
              <IconArrowUp size={8} />
            ) : (
              <IconArrowDown size={8} />
            )}
          </SC.DropDownArrow>
        </SC.DropDownTrigger>
        {this.state.isOpen && (
          <SC.DropDownShadow isOpen={this.state.isOpen}>
            <SC.DropDownDrawer
              innerRef={node => {
                this.refDrawer = node;
              }}
              isOpen={this.state.isOpen}
            >
              {this.props.items.map((item, key) => {
                if (item.isHeader) {
                  return (
                    <SC.DropDownHeader key={key}>{item.text}</SC.DropDownHeader>
                  );
                } else {
                  return (
                    <SC.DropDownItem
                      key={key}
                      value={item.value}
                      onClick={this.selectItem.bind(this, key)}
                      onMouseEnter={this.mouseEnterItem.bind(this, key)}
                      hover={this.state.highlightedItem === key}
                      selected={this.state.selectedIndex === key}
                      disabled={item.disabled}
                    >
                      {item.text}
                    </SC.DropDownItem>
                  );
                }
              })}
            </SC.DropDownDrawer>
          </SC.DropDownShadow>
        )}
        <select
          id={this.props.id}
          value={
            (this.state.selectedIndex &&
              this.props.items[this.state.selectedIndex].value) ||
            ''
          }
          disabled={disabled}
          name={this.props.name}
          required={this.props.required}
          onKeyDown={this.selectKeyPress.bind(this)}
          ref={node => {
            this.refSelect = node;
          }}
          readOnly
        >
          {this.props.items.map((item, key) => {
            const { value, disabled } = item;
            let optionProps = { value, disabled };
            return (
              <option key={key} {...optionProps}>
                {item.text}
              </option>
            );
          })}
        </select>
      </SC.DropDownWrapper>
    );
  }
}

DropDown.defaultProps = {
  items: [],
  id: '',
  placeholder: '',
  selectedIndex: null,
  onChange: noop
};

DropDown.propTypes = {
  /** ID to use in conjunction with label etc. */
  id: PropTypes.string,
  /** Children inside component */
  children: PropTypes.node,
  /** Name */
  name: PropTypes.string,
  /** Sets whether input is required */
  required: PropTypes.bool,
  /** Disable the dropdown field */
  disabled: PropTypes.bool,
  /** onChange handler */
  onChange: PropTypes.func,
  /** index of the selected item */
  selectedIndex: PropTypes.number,
  /** Placeholder text to use when drop down has no selection */
  placeholder: PropTypes.string,

  /** List of item objects for the dropdown content */
  items: PropTypes.arrayOf(
    PropTypes.shape({
      value: PropTypes.string,
      text: PropTypes.string.isRequired,

      /** Flag indicates if the item is an header
       * For header items, the `value` field is redundant */
      isHeader: PropTypes.bool,

      /** Flag indicates if the item is selectable by the user
       * by interacting with the in the dropdown.
       * If set to `true`, the item is not selectable
       *
       * Similar to how HTML <option> disabled attribute behaves. */
      disabled: PropTypes.bool
    })
  ).isRequired
};

DropDown.legacyName = 'ux-select';
export default DropDown;
