import styled, { css } from 'styled-components';

import Theme from '../../theme';

export default {
  RibbonWrapper: styled.div`
    position: relative;
    width: 100%;
    height: 0;
    overflow: visible;
  `,
  Ribbon: styled.div`
    ${Theme.fonts.styles.SubscriptBadge};
    line-height: 0.75rem;
    background-color: ${Theme.colours.green_BF};
    color: ${Theme.colours.white};
    padding: 0.5rem 0.625rem;
    text-transform: uppercase;
    position: absolute;
    display: inline-block;

    ::before {
      content: ' ';
      height: 2px;
      position: absolute;
      bottom: -2px;
      right: 8px;
      left: 3px;
      background-color: ${Theme.colours.black};
      opacity: 0.1;
    }

    ::after {
      content: ' ';
      width: 0;
      height: 0;
      border-bottom: 5px solid ${Theme.colours.green_BF};
      position: absolute;
      bottom: -5px;
      transform: rotate(-180deg);
    }

    ${props => {
      if (props.mountTo === 'left') {
        return css`
          left: -8px;

          ::before {
            right: 3px;
            left: 8px;
          }

          ::after {
            left: 0;
            border-right: 8px solid transparent;
          }
        `;
      } else {
        return css`
          right: -8px;

          ::before {
            right: 8px;
            left: 3px;
          }

          ::after {
            right: 0;
            border-left: 8px solid transparent;
          }
        `;
      }
    }};
  `
};
