import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Row from '../Row';
import Column from '../Column';
import SC from './styles';

class Tabs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: this.props.activeTab
    };
  }

  handleTabChange(selectedTab) {
    this.setState({
      activeTab: selectedTab.value
    });
    this.props.onTabChange(selectedTab);
  }

  render() {
    const { Anchor, AnchorSelected, TabContainer, TabItem, TabList } = SC;

    return (
      <TabContainer>
        <Row>
          <Column sm={12}>
            <TabList isInline={true}>
              {this.props.tabItems.map(tabItem => (
                <TabItem key={tabItem.value}>
                  {this.state.activeTab === tabItem.value ? (
                    <AnchorSelected
                      onClick={() => this.handleTabChange(tabItem)}
                      disabled={tabItem.disabled}
                    >
                      {tabItem.text}
                    </AnchorSelected>
                  ) : (
                    <Anchor
                      onClick={() => this.handleTabChange(tabItem)}
                      disabled={tabItem.disabled}
                    >
                      {tabItem.text}
                    </Anchor>
                  )}
                </TabItem>
              ))}
            </TabList>
          </Column>
        </Row>
      </TabContainer>
    );
  }
}

Tabs.defaultProps = {
  activeTab: '',
  onTabChange: function() {},
  tabItems: []
};

Tabs.propTypes = {
  /** Class name */
  className: PropTypes.node,
  /** Children inside component */
  children: PropTypes.node,
  /** Active tab value*/
  activeTab: PropTypes.string,
  /** Event to fire on tab change*/
  onTabChange: PropTypes.func,
  /** List of tab items*/
  tabItems: PropTypes.arrayOf(
    PropTypes.shape({
      text: PropTypes.string,
      value: PropTypes.string,
      disabled: PropTypes.bool
    })
  )
};

Tabs.legacyName = ['ux-tablinks', 'ux-tablink'];
export default Tabs;
