import React from 'react';
import PropTypes from 'prop-types';
import sc from './styles';
import sgFlag from '../../assets/images/flag-65.png';

const PhoneNumberField = props => {
  return (
    <sc.Wrapper>
      <sc.Input {...props} />
      {props.countryCode && (
        <sc.CountryCode>
          <img
            alt={props.countryCode}
            src={props.countryFlagSrc ? props.countryFlagSrc : sgFlag}
          />
          <sc.Code> +{props.countryCode}</sc.Code>
        </sc.CountryCode>
      )}
    </sc.Wrapper>
  );
};

PhoneNumberField.defaultProps = {
  isEditable: true,
  type: 'tel',
  countryCode: 65
};

PhoneNumberField.propTypes = {
  /** Anchor text */
  children: PropTypes.node,
  /** Extra class to apply to element */
  className: PropTypes.string,
  /** ID of input */
  id: PropTypes.string,
  /** Disable the element */
  disabled: PropTypes.bool,
  /** Maximum value length */
  maxLength: PropTypes.number,
  /** Name of input field */
  name: PropTypes.string,
  /** Placeholder text */
  placeholder: PropTypes.string,
  /** Value binded to this element */
  value: PropTypes.string,
  /** On blur function */
  onBlur: PropTypes.func,
  /** On change function */
  onChange: PropTypes.func,
  /** On click function */
  onClick: PropTypes.func,
  /** On focus function */
  onFocus: PropTypes.func,
  /** On select function */
  onSelect: PropTypes.func,
  /** On key down function */
  onKeyDown: PropTypes.func,
  /** On key press function */
  onKeyPress: PropTypes.func,
  /** On key up function */
  onKeyUp: PropTypes.func,
  /** Is Editable **/
  isEditable: PropTypes.bool,
  /** Input type, defaults to tel if left unset*/
  type: PropTypes.string,
  /** Input countryCode */
  countryCode: PropTypes.number,
  /** Input country flag image src, image file should be 52x52 and defaults to SG flag if left unset */
  countryFlagSrc: PropTypes.string
};

export default PhoneNumberField;
