import React, { Component } from 'react';
import PropTypes from 'prop-types';
import sc from './styles';
import { noop } from '@lux/helpers';

class TextArea extends Component {
  constructor(props) {
    super(props);
    this.handleRemainder = this.handleRemainder.bind(this);
    this.state = {
      textareaRefLength: 0
    };
  }

  handleRemainder() {
    if (this.props.maxLength && this.props.showRemainingChar) {
      this.setState({ textareaRefLength: this.textareaRef.value.length });
    }
  }

  render() {
    return (
      <React.Fragment>
        <sc.TextArea
          id={this.props.id}
          autoComplete={this.props.autoComplete}
          className={this.props.className}
          disabled={!this.props.isEditable || this.props.disabled}
          form={this.props.form}
          maxLength={this.props.maxLength}
          name={this.props.name}
          pattern={this.props.pattern}
          placeholder={this.props.placeholder}
          size={this.props.size}
          type="text"
          value={this.props.value}
          onBlur={this.props.onBlur}
          onChange={e => {
            this.props.onChange(e);
            this.handleRemainder();
          }}
          onClick={this.props.onClick}
          onFocus={this.props.onFocus}
          onSelect={this.props.onSelect}
          onKeyDown={this.props.onKeyDown}
          onKeyPress={this.props.onKeyPress}
          onKeyUp={this.props.onKeyUp}
          isEditable={this.props.isEditable}
          maxHeight={this.props.maxHeight}
          resizeType={this.props.resizeType}
          innerRef={textarea => {
            this.textareaRef = textarea;
          }}
          error={this.props.error}
        />
        {this.props.maxLength &&
          this.props.showRemainingChar && (
            <sc.RmChar>
              {this.props.maxLength - this.state.textareaRefLength} characters
              remaining
            </sc.RmChar>
          )}
      </React.Fragment>
    );
  }
}

TextArea.defaultProps = {
  isEditable: true,
  onBlur: noop,
  onChange: noop,
  onClick: noop,
  onFocus: noop,
  onSelect: noop,
  onKeyDown: noop,
  onKeyPress: noop,
  onKeyUp: noop
};

TextArea.propTypes = {
  /** Anchor text */
  children: PropTypes.node,
  /** Extra class to apply to element */
  className: PropTypes.string,
  /** ID of input */
  id: PropTypes.string,
  /** Disable the element */
  disabled: PropTypes.bool,
  /** Maximum value length */
  maxLength: PropTypes.number,
  /** Name of input field */
  name: PropTypes.string,
  /** Placeholder text */
  placeholder: PropTypes.string,
  /** Value binded to this element */
  value: PropTypes.string,
  /** On blur function */
  onBlur: PropTypes.func,
  /** On change function */
  onChange: PropTypes.func,
  /** On click function */
  onClick: PropTypes.func,
  /** On focus function */
  onFocus: PropTypes.func,
  /** On select function */
  onSelect: PropTypes.func,
  /** On key down function */
  onKeyDown: PropTypes.func,
  /** On key press function */
  onKeyPress: PropTypes.func,
  /** On key up function */
  onKeyUp: PropTypes.func,
  /** Is Editable **/
  isEditable: PropTypes.bool,
  /** Max height of the Text Area units in px **/
  maxHeight: PropTypes.number,
  /** Resize type, if left unset defaults to none, values available: both | horizontal | vertical | initial | inherit */
  resizeType: PropTypes.string,
  /** Shows how many remaining characters are available in the TextArea on the bottom right side, must set maxLength for this to work*/
  showRemainingChar: PropTypes.bool,
  /** Error with red border*/
  error: PropTypes.bool
};

export default TextArea;
