import styled from 'styled-components';
import Theme from '../../theme';

export default {
  CheckBoxWrapper: styled.span`
    display: inline-block;
    height: 1.5rem;
    width: 1.5rem;
    overflow: hidden;
    position: relative;

    input {
      position: absolute;
      top: 0;
      left: -99999px;

      & + span {
        height: 100%;
        width: 100%;
        display: block;
        cursor: pointer;
        border-radius: 5px;
        border: 1px solid ${Theme.colours.grey_c6};
      }

      &:checked + span {
        border: 1px solid ${Theme.colours.linkblue};
      }

      &:disabled + span {
        border: 1px solid ${Theme.colours.grey_c6};
        background-color: ${Theme.colours.grey_f5};
      }

      &:not(:disabled) + span:hover {
        border: 1px solid ${Theme.colours.linkblue};
      }
    }
  `
};
