import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import defaultTheme from '../../theme';

const Grid = styled.div`
  margin-right: auto;
  margin-left: auto;
  max-width: 100%;
  padding-right: ${p => p.theme.grid.outerMargin / 2}rem;
  padding-left: ${p => p.theme.grid.outerMargin / 2}rem;

  ${p =>
    p.fluid &&
    `
      padding-right: ${p.theme.grid.outerMargin}rem;
      padding-left: ${p.theme.grid.outerMargin}rem;
  `};

  ${p =>
    !p.fluid &&
    css`
      ${['md', 'lg'].map(
        viewport => p.theme.media[viewport]`
        width: ${p.theme.grid.container[viewport]}rem;
      `
      )};
    `};
`;

Grid.defaultProps = {
  theme: defaultTheme,
  fluid: false
};

Grid.propTypes = {
  /**
   * Create a responsive fixed width Grid or a
   * full width Grid, spanning the entire width
   * of your viewport. */
  fluid: PropTypes.bool
};

/** @component */
export default Grid;
