import styled, { css } from 'styled-components';

import Theme from '../../theme';
import { remCalc } from '@lux/helpers';

export default {
  TextArea: styled.textarea`
    font-family: ${Theme.fonts.families.AvenirBook};
    font-size: ${Theme.fonts.sizes.regular};
    ${Theme.styles.componentSmallRadius};
    height: ${props =>
      props.maxHeight ? remCalc(props.maxHeight) : remCalc(40)};
    border: 1px solid ${Theme.colours.grey_c6};
    box-shadow: none;
    padding: ${remCalc(8)} ${remCalc(12)};
    width: 100%;
    margin: 0 0 ${remCalc(16)} 0;
    color: ${Theme.colours.black};
    display: block;
    resize: ${props => (props.resizeType ? props.resizeType : 'none')};

    ${props =>
      !props.isEditable
        ? css`
            font-size: ${Theme.fonts.sizes.large};
            border: none;
            padding: ${remCalc(8)} 0;
            color: ${Theme.colours.black};
          `
        : css`
            &.disabled,
            &[disabled] {
              background: ${Theme.colours.white};
              border: 1px solid ${Theme.colours.grey_cc};
              color: ${Theme.colours.grey_cc};
              cursor: default;
              transition: border-color 0.15s linear, background 0.15s linear;
            }
          `};

    && {
      ${props =>
        props.error ? `border-color: ${Theme.colours.rederror}` : ''};
    }

    &::placeholder {
      color: ${Theme.colours.grey_c6};
      font-size: ${remCalc(16)};
    }

    &:focus {
      border-color: ${Theme.colours.linkblue};
      outline: 0;
    }
  `,
  RmChar: styled.p`
    color: ${Theme.colours.grey_a1};
    text-align: right;
    font-size: ${Theme.fonts.sizes.xsmall};
  `
};
