import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Theme from '../../theme';
import { remCalc } from '@lux/helpers';

const SCButton = styled.a`
  display: inline-block;
  cursor: pointer;
  text-decoration: none;
  transition: background-color 0.3s ease-out;
  text-align: center;
  font-family: ${Theme.fonts.families.AvenirHeavy};
  min-width: 100%;
  max-height: ${remCalc(40)};
  height: ${remCalc(40)};
  width: auto;
  padding: ${remCalc(8.8)} ${remCalc(40)};
  border-radius: ${remCalc(20)};
  border-width: 2px;
  border-style: solid;
  border-color: ${Theme.colours.singtelred};
  font-size: ${remCalc(16)};
  line-height: initial;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none;
  ${Theme.media.md`
    min-width: ${remCalc(100)};
    border-width: 1px;
  `} color: ${props =>
  props.secondary ? Theme.colours.singtelred : Theme.colours.white};
  background-color: ${props =>
    props.secondary ? Theme.colours.white : Theme.colours.singtelred};

  &[disabled] {
    outline: 0;
    color: ${props =>
      props.secondary ? Theme.colours.grey_cc : Theme.colours.white};
    background-color: ${props =>
      props.secondary ? Theme.colours.white : Theme.colours.grey_cc};
    border-color: ${Theme.colours.grey_cc};
    cursor: default;
  }

  &:not([disabled]) {
    &:hover {
      color: ${Theme.colours.white};
      background-color: ${Theme.colours.singtelred_l1};
      border-color: ${Theme.colours.singtelred_l1};
    }
  }
`;

class Button extends Component {
  render() {
    return (
      <SCButton
        type={this.props.type}
        className={this.props.className}
        id={this.props.id}
        href={this.props.href}
        role={this.props.role}
        aria-label={this.props.ariaLabel}
        target={this.props.target}
        title={this.props.title}
        tabIndex={this.props.tabIndex}
        disabled={this.props.disabled}
        onClick={this.handleClick.bind(this)}
        secondary={this.props.secondary}
      >
        {this.props.children}
      </SCButton>
    );
  }

  handleClick(e) {
    if (!this.props.disabled) {
      // we need to check for onClick because e.preventDefault() will stop
      // the button from firing window.location change when a href has been
      // supplied...
      if (this.props.onClick) {
        e.preventDefault();
        this.props.onClick(e);
      }
    }
  }
}

Button.defaultProps = {
  disabled: false,
  secondary: false
};

Button.propTypes = {
  /** Button text */
  children: PropTypes.node,
  /** Extra class to apply to element */
  className: PropTypes.string,
  /** ID of button */
  id: PropTypes.string,
  /** The link for the button tag */
  href: PropTypes.string,
  /** Set the role of the button */
  role: PropTypes.string,
  /** Set the aria label */
  ariaLabel: PropTypes.string,
  /** Set the target of the link */
  target: PropTypes.string,
  /** Add a title to the link */
  title: PropTypes.string,
  /** Set the tab index */
  tabIndex: PropTypes.number,
  /** Event to fire when component is clicked */
  onClick: PropTypes.func,
  /** Disable button */
  disabled: PropTypes.bool,
  /** Invert the colours */
  type: PropTypes.string,
  /** Take the full width of parent */
  block: PropTypes.bool
};

export default Button;
