import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SC from './styles';

import Theme from '../../theme';

import { hexToRGBA, rgbaToHex, adjustContrast } from '@lux/helpers';

class UsageBar extends Component {
  render() {
    return (
      <SC.UsageBar className={this.props.className}>
        <SC.UsageDetails>
          <strong>
            {this.props.usedValue} {this.props.units}
          </strong>{' '}
          /
          {this.props.isUnlimited
            ? ` Unlimited ${this.props.postValue} `
            : ` ${this.props.totalValue} ${this.props.units} ${
                this.props.postValue
              } `}
          {this.props.usedValue > this.props.totalValue &&
            this.props.isUnlimited === false && (
              <SC.ExceededAmount>
                (Exceeded limit by{' '}
                {this.props.usedValue - this.props.totalValue}{' '}
                {this.props.units})
              </SC.ExceededAmount>
            )}
        </SC.UsageDetails>

        <SC.BarOuter>
          <SC.BarInner
            size={this.calculateBarValue()}
            primaryColour={
              this.props.usedValue <= this.props.totalValue ||
              this.props.isUnlimited
                ? Theme.colours[this.props.primaryColour]
                : Theme.colours['rederror']
            }
            secondaryColour={
              this.props.isUnlimited
                ? rgbaToHex(
                    adjustContrast(
                      hexToRGBA(Theme.colours[this.props.primaryColour]),
                      10
                    ),
                    true
                  )
                : null
            }
          />
        </SC.BarOuter>
      </SC.UsageBar>
    );
  }

  calculateBarValue() {
    let value;
    if (
      this.props.usedValue > this.props.totalValue ||
      this.props.isUnlimited
    ) {
      value = 100;
    } else {
      value = this.props.usedValue / this.props.totalValue * 100;
    }
    return value;
  }
}

UsageBar.defaultProps = {
  usedValue: 0,
  totalValue: 100,
  isUnlimited: false,
  units: '',
  postValue: ''
};

UsageBar.propTypes = {
  /* Classes applied to the component */
  className: PropTypes.node,
  /* The consumed amount of the bar */
  usedValue: PropTypes.number,
  /* units to display for usage (MB, GB, mins, quantity, etc... */
  units: PropTypes.string,
  /* The 100% value of the bar */
  totalValue: PropTypes.number,
  /* String to display to add context to value measurement (Data, SMS, Talktime, etc...) */
  postValue: PropTypes.string,
  /* Colour class to be used for the usage */
  primaryColour: PropTypes.string,
  /* if bar is to display unlimited measurement - it will not display a total */
  isUnlimited: PropTypes.bool
};

export default UsageBar;
