/* istanbul ignore file */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Anchor from '../Anchor';
import IconClose from '../../assets/svgs/close.svg';

import sc from './styles';

import { noop } from '../../helpers';

class Modal extends Component {
  constructor(props) {
    super(props);
    // Set open state based on default props
    this.state = {
      open: props.open
    };
  }

  componentWillReceiveProps(nextProps) {
    // If we have recieved an updated prop to open/close
    if (nextProps.open !== this.state.open) {
      this.setState({ open: nextProps.open });
    }
  }

  render() {
    const ModalLink = () => {
      return (
        <Anchor onClick={this.handleTextClick.bind(this)}>
          {this.props.text}
        </Anchor>
      );
    };
    const ModalWindow = () => {
      if (!this.props.showClose) {
        return <sc.ModalWindow>{this.props.children}</sc.ModalWindow>;
      } else {
        return (
          <sc.ModalWindow>
            <sc.Close onClick={this.close.bind(this)} />
            {this.props.children}
          </sc.ModalWindow>
        );
      }
    };

    return (
      <div className={this.props.className}>
        {this.props.text && <ModalLink />}
        <div>
          {this.state.open && (
            <sc.ModalWrapper>
              <sc.Overlay onClick={this.close.bind(this)} />
              <ModalWindow />
            </sc.ModalWrapper>
          )}
        </div>
      </div>
    );
  }

  handleTextClick() {
    this.setState({ open: true });
  }

  close(e) {
    e.preventDefault();
    if (!this.props.disableClose) {
      this.setState({ open: false });
      this.props.onClose(e);
    }
  }
}

Modal.defaultProps = {
  open: false,
  disableClose: false,
  onClose: noop,
  showClose: false
};

Modal.propTypes = {
  /** Extra class to apply to element */
  className: PropTypes.string,
  /** If modal is open by default */
  open: PropTypes.bool,
  /**
   * If set to true, modal does not show the close button.
   * And modal does not close if the user clicks on the backdrop.
   * */
  disableClose: PropTypes.bool,
  /** Handler when modal closes */
  onClose: PropTypes.func,
  /** If set, it will appear as Anchor text to open modal */
  text: PropTypes.string,
  /**
   * If set to true, modal does  show the close sign.
   * */
  showClose: PropTypes.bool
};

export default Modal;
