import styled, { css } from 'styled-components';

import Theme from '../../theme';

const DropDownItem = styled.div`
  display: block;
  width: 100%;
  height: 2.5rem;
  padding: 0.75rem;
  cursor: pointer;
  color: ${Theme.colours.black};
  z-index: 1;
`;

export default {
  DropDownWrapper: styled.div`
    font-family: ${Theme.fonts.families.AvenirBook};
    display: inline-block;
    width: 100%;
    height: 2.5rem;
    user-select: none;
    z-index: 4;
    position: ${p => (p.isOpen ? 'relative' : 'inherit')};

    select {
      position: absolute;
      left: -9999px;
    }
  `,
  DropDownShadow: styled.div`
    position: absolute;
    top: 0;
    width: inherit;
    z-index: 3;
    box-shadow: 0.125rem 0.125rem ${'#E5E5E550'};
    padding-top: 2.5rem;
  `,
  DropDownTrigger: styled.div`
    border: 1px solid ${Theme.colours.grey_c6};
    height: 2.5rem;
    width: 100%;
    border-radius: 2.5px;
    cursor: pointer;
    padding: 0.5rem 0.75rem;
    position: relative;
    z-index: 3;

    ${props => {
      if (props.isOpen === true) {
        return css`
          border-bottom-color: ${Theme.colours.white};
          border-bottom-right-radius: 0;
          border-bottom-left-radius: 0;
        `;
      }
    }};
  `,
  DropDownArrow: styled.div`
    position: absolute;
    right: 0.5rem;
    top: 0;
    bottom: 0;
    width: 8px;
    height: 100%;
    z-index: 1;
    display: flex;
    align-items: center;

    path {
      fill: ${Theme.colours.grey_a1};
    }
  `,
  DropDownDrawer: styled.div`
    color: ${Theme.colours.black};
    border: 1px solid ${Theme.colours.grey_c6};
    display: block;
    background-color: ${Theme.colours.white};
    width: 100%;
    border-bottom-right-radius: 2.5px;
    border-bottom-left-radius: 2.5px;
    max-height: 19rem;
    overflow-y: auto;
  `,
  DropDownItem: styled(DropDownItem)`
    line-height: 1rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;

    ${props =>
      props.hover &&
      css`
        background-color: ${Theme.colours.grey_f5};
        color: ${Theme.colours.black};
      `};

    ${props =>
      props.selected &&
      css`
        font-family: ${Theme.fonts.families.AvenirHeavy};
        color: ${Theme.colours.singtelred};
      `};

    ${props =>
      props.disabled &&
      css`
        color: ${Theme.colours.grey_c6};
      `};
  `,
  DropDownHeader: styled(DropDownItem)`
    font-family: ${Theme.fonts.families.AvenirHeavy};
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    cursor: default;
  `,
  SelectedValue: styled.div`
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 10px;
    width: 100%;
    height: 100%;
  `,
  Placeholder: styled.div`
    color: ${Theme.colours.grey_c6};
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 10px;
    width: 100%;
    text-transform: capitalize;
    height: 100%;
  `
};
