import React from 'react';
import PropTypes from 'prop-types';
import Row from '../Row';
import { noop } from '../../helpers';

import SC from './styles';

const ImageTextCard = props => {
  const {
    image,
    imageAltText,
    name,
    description,
    onClickHandler,
    strikeThroughText,
    ribbonText
  } = props;
  return (
    <SC.ImageTextCard onClick={onClickHandler}>
      <Row middle="sm">
        <SC.ImageWrapper sm={4} md={12}>
          {ribbonText && <SC.RibbonText>{ribbonText}</SC.RibbonText>}
          <SC.ImageTextCardImage src={image} alt={imageAltText} />
        </SC.ImageWrapper>
        <SC.ImageTextCardDescription sm={8} md={12}>
          <Row start="sm">
            <SC.DescriptionWrapper>
              {ribbonText && (
                <SC.RibbonWrapper>
                  <SC.RibbonTextOnDescription>
                    {ribbonText}
                  </SC.RibbonTextOnDescription>
                </SC.RibbonWrapper>
              )}
              <SC.Bold>{name}</SC.Bold>
            </SC.DescriptionWrapper>
          </Row>
          <Row start="sm">
            <SC.PriceVariant>
              {description}
              {strikeThroughText && (
                <SC.StrikeThroughText>{strikeThroughText}</SC.StrikeThroughText>
              )}
            </SC.PriceVariant>
          </Row>
        </SC.ImageTextCardDescription>
      </Row>
    </SC.ImageTextCard>
  );
};

ImageTextCard.defaultProps = {
  onClickHandler: noop,
  image: '',
  imageAltText: '',
  name: '',
  description: '',
  strikeThroughText: '',
  ribbonText: ''
};

ImageTextCard.propTypes = {
  /** ImageTextCard image src*/
  image: PropTypes.string,
  /** ImageTextCard image alt text */
  imageAltText: PropTypes.string,
  /** ImageTextCard name */
  name: PropTypes.string,
  /** ImageTextCard description string */
  description: PropTypes.string,
  /** gift card onclick handler */
  onClickHandler: PropTypes.func,
  /** strike-through text */
  strikeThroughText: PropTypes.string,
  /** ribbon text */
  ribbonText: PropTypes.string
};

export default ImageTextCard;
