import styled from 'styled-components';
import { remCalc } from '@lux/helpers';
import Theme from '../../theme';

export default {
  ToggleSwitch: styled.div`
    width: ${remCalc(48)};
    height: ${remCalc(24)};
  `,
  Label: styled.label`
    position: relative;
    padding: 1px;
  `,
  CheckBox: styled.input`
    display: none;
  `,
  P: styled.p`
    color: ${Theme.colours.white};
    font-family: ${Theme.fonts.families.Arial};
    font-size: 10px;
    margin: 0;
    position: absolute;
    z-index: 1;
    top: 5px;
    left: ${props => (props.active ? remCalc(5) : remCalc(26))};
  `,
  Span: styled.span`
    position: absolute;
    cursor: pointer;
    left: 0;
    right: 0;
    top: 0;
    background-color: ${props =>
      !props.disabled
        ? props.active
          ? Theme.colours.linkblue
          : Theme.colours.grey_c6
        : Theme.colours.grey_f5};

    transition: 0.4s;
    border-radius: ${remCalc(12)};
    width: ${remCalc(48)};
    height: ${remCalc(24)};
    border: ${props =>
      props.disabled ? '1px solid ' + Theme.colours.grey_c6 : 'none'};

    &::before {
      position: absolute;
      content: '';
      height: ${remCalc(22)};
      width: ${remCalc(22)};
      left: ${props => (props.disabled ? '-1px' : '1px')};
      top: ${props => (props.disabled ? '0' : '1px')};
      background-color: ${props =>
        !props.disabled ? Theme.colours.white : Theme.colours.grey_f5};
      transition: 0.4s;
      border-radius: 12px;
      transform: translate(${props => (props.active ? remCalc(24) : '0px')});
      border: ${props =>
        props.disabled ? '1px solid ' + Theme.colours.grey_c6 : 'none'};
    }
  `
};
