import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import DayPickerSingleDateController from 'react-dates/lib/components/DayPickerSingleDateController';
import SingleDatePicker from 'react-dates/lib/components/SingleDatePicker';
import moment from 'moment';
import ArrowLeft from '../../assets/svgs/arrow-left.svg';
import ArrowRight from '../../assets/svgs/arrow-right.svg';

import Theme from '../../theme';
import SC from './styles';

class Calendar extends Component {
  constructor(props) {
    super(props);

    const { initialDate, startDate, endDate } = this.props;
    const _initialDate = initialDate ? moment(initialDate, 'DD/MM/YYYY') : null;
    const _startDate = startDate ? moment(startDate, 'DD/MM/YYYY') : null;
    const _endDate = endDate ? moment(endDate, 'DD/MM/YYYY') : null;

    this.state = {
      date: _initialDate,
      currentNav: _startDate,
      startDate: _startDate,
      endDate: _endDate,
      focused: false
    };
  }

  onDateChange = date => {
    this.setState({ date });
    this.props.onDateChange && this.props.onDateChange(date);
  };

  onFocusChange = ({ focused }) => {
    this.setState({ focused });
  };

  _isOutsideRange = day => {
    const { startDate, endDate } = this.props;
    const _startDate = moment(startDate, 'DD/MM/YYYY');
    const _endDate = moment(endDate, 'DD/MM/YYYY');

    // If start and end date are not set, we consider it not
    // outside the range so the full calendar is available
    if (!_startDate.isValid() || !_endDate.isValid()) {
      return false;
    }

    return !day.isBetween(_startDate, _endDate, 'days', '[]');
  };

  handleNavClick = date => this.setState({ currentNav: date });

  renderNavNext = (endDate, currentNav, allowNavigation) => {
    if (
      allowNavigation ||
      !endDate ||
      !endDate.isSameOrBefore(currentNav, 'month')
    ) {
      return (
        <SC.StyledNextNav>
          <ArrowRight fill={Theme.colours.grey_c6} />
        </SC.StyledNextNav>
      );
    }

    return <div />;
  };

  renderNavPrev = (startDate, currentNav, allowNavigation) => {
    if (
      allowNavigation ||
      !startDate ||
      !startDate.isSameOrAfter(currentNav, 'month')
    ) {
      return (
        <SC.StyledNextNav>
          <ArrowLeft fill={Theme.colours.grey_c6} />
        </SC.StyledNextNav>
      );
    }

    return <div />;
  };

  render() {
    const { date, startDate, endDate, currentNav } = this.state;
    const { noBorder, allowNavigation, type } = this.props;

    switch (type) {
      case 'SingleDatePicker':
        return (
          <SC.StyledWrapper>
            <SC.StyledContent isSelected={date || null}>
              <SingleDatePicker
                hideKeyboardShortcutsPanel
                onDateChange={this.onDateChange}
                isOutsideRange={this._isOutsideRange}
                focused={this.state.focused}
                date={date}
                numberOfMonths={1}
                onPrevMonthClick={this.handleNavClick}
                onNextMonthClick={this.handleNavClick}
                navNext={this.renderNavNext(
                  endDate,
                  currentNav,
                  allowNavigation
                )}
                navPrev={this.renderNavPrev(
                  startDate,
                  currentNav,
                  allowNavigation
                )}
                noBorder={noBorder}
                onFocusChange={this.onFocusChange}
                displayFormat="DD/MM/YYYY"
              />
            </SC.StyledContent>
          </SC.StyledWrapper>
        );
      case 'DayPicker':
        return (
          <SC.StyledWrapper>
            <SC.StyledContent>
              <DayPickerSingleDateController
                hideKeyboardShortcutsPanel
                onDateChange={this.onDateChange}
                isOutsideRange={this._isOutsideRange}
                focused={true}
                date={date}
                onPrevMonthClick={this.handleNavClick}
                onNextMonthClick={this.handleNavClick}
                navNext={this.renderNavNext(
                  endDate,
                  currentNav,
                  allowNavigation
                )}
                navPrev={this.renderNavPrev(
                  startDate,
                  currentNav,
                  allowNavigation
                )}
                noBorder={noBorder}
              />
            </SC.StyledContent>
          </SC.StyledWrapper>
        );
      default:
        return null;
    }
  }
}

Calendar.defaultProps = {
  className: '',
  type: 'DayPicker',
  noBorder: false,
  allowNavigation: false
};

Calendar.propTypes = {
  /** Class name */
  className: PropTypes.node,
  /** Type of calendar: one of 'SingleDayPicker', 'DayPicker' */
  type: PropTypes.string.isRequired,
  /** Beginning of selectable days in the format "DD/MM/YYYY" */
  startDate: PropTypes.string,
  /** End of selectable days in the format "DD/MM/YYYY" */
  endDate: PropTypes.string,
  /** Initial Selected Date in the format "DD/MM/YYYY" Defaults to current date if omitted */
  initialDate: PropTypes.string,
  /** Called when user selects a date. Returns a Moment object */
  onDateChange: PropTypes.func,
  /** Calendar can have no border */
  noBorder: PropTypes.bool,
  /** Allow users to navigate outside of start and end date*/
  allowNavigation: PropTypes.bool
};

Calendar.legacyName = 'ux-calendar';
export default Calendar;
