import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Button from '../Button';
import Anchor from '../Anchor';
import SC from './styles';

import ConditionalWrap from '../../helpers/ConditionalWrap';

class CatalogueCard extends Component {
  render() {
    const {
      ctaSecondary,
      ctaText,
      ctaType,
      ctaUrl,
      description,
      footnote,
      image,
      imageAlt,
      imageComponent,
      linkComponent,
      onClick,
      price,
      pricePreText,
      pricePostText,
      ribbonText,
      title,
      titleStyle,
      descriptionStyle
    } = this.props;

    const Ribbon = () => <SC.Ribbon>{ribbonText}</SC.Ribbon>;

    // This component has the ability of replacing the image
    // component with a substitute through props
    const Image = () =>
      imageComponent || <SC.Image src={image} alt={imageAlt} />;

    const Title = () => (
      <SC.Title innerRef={node => (this.refTitle = node)} style={titleStyle}>
        {title}
      </SC.Title>
    );

    const Description = () => (
      <SC.Description
        innerRef={node => (this.refDescription = node)}
        style={descriptionStyle}
      >
        {description}
      </SC.Description>
    );

    const Footnote = () => <SC.TC>{footnote}</SC.TC>;

    const Price = () => (
      <React.Fragment>
        <SC.PrePrice>{pricePreText}</SC.PrePrice>
        <SC.Price>{price}</SC.Price>
        <SC.PostPrice>{pricePostText}</SC.PostPrice>
      </React.Fragment>
    );

    const CtaAnchor = () => (
      <SC.CTA>
        <Anchor secondary={ctaSecondary} href={ctaUrl} onClick={onClick}>
          {ctaText}
        </Anchor>
      </SC.CTA>
    );

    const CtaButton = () => (
      <SC.CTA>
        <Button secondary={ctaSecondary} href={ctaUrl} onClick={onClick}>
          {ctaText}
        </Button>
      </SC.CTA>
    );

    const CtaComponent = ({ children }) => (
      <a href={ctaUrl} onClick={onClick}>
        {children}
      </a>
    );

    const ConditionalCta = ({ children }) => (
      <ConditionalWrap
        condition={ctaType === 'component'}
        wrap={children =>
          // This component has the ability of replacing the link
          // component with a substitute through props
          linkComponent ? (
            linkComponent(children)
          ) : (
            <CtaComponent>{children}</CtaComponent>
          )
        }
      >
        {children}
      </ConditionalWrap>
    );

    return (
      <ConditionalCta>
        <SC.CatalogueCard>
          <SC.Card>
            {ribbonText && <Ribbon />}
            <Image />

            <SC.Content>
              <Title />
              <Description />
              {price && <Price />}
              {footnote && <Footnote />}
            </SC.Content>

            {ctaType === 'button' && <CtaButton />}
            {ctaType === 'anchor' && <CtaAnchor />}
          </SC.Card>
        </SC.CatalogueCard>
      </ConditionalCta>
    );
  }
}

CatalogueCard.defaultProps = {
  ctaType: 'anchor'
};

CatalogueCard.propTypes = {
  /** Ribbon text */
  ribbonText: PropTypes.string,
  /** Image Path */
  image: PropTypes.string,
  /** Img Alt text */
  imageAlt: PropTypes.string,
  /** Image replacement component */
  imageComponent: PropTypes.element,
  /** Link replacement component */
  linkComponent: PropTypes.func,
  /** Title text */
  title: PropTypes.string,
  /** */
  pricePreText: PropTypes.string,
  /** */
  price: PropTypes.string,
  /** */
  pricePostText: PropTypes.string,
  /** Foot note text */
  footnote: PropTypes.string,
  /** CTA Type */
  ctaType: PropTypes.oneOf(['anchor', 'button', 'component']),
  /** CTA Secondary Style */
  ctaSecondary: PropTypes.bool,
  /** CTA Url */
  ctaUrl: PropTypes.string,
  /** CTA Text */
  ctaText: PropTypes.string,
  /** Click handler on the CTA */
  onClick: PropTypes.func,
  /* Spread style into title element */
  titleStyle: PropTypes.object,
  /* Spread style into description element */
  descriptionStyle: PropTypes.object
};

export default CatalogueCard;
