import React from 'react';
import PropTypes from 'prop-types';
import SC from './styles';

const Ribbon = props => {
  return (
    <SC.RibbonWrapper className={props.className}>
      <SC.Ribbon mountTo={props.mountTo}>{props.children}</SC.Ribbon>
    </SC.RibbonWrapper>
  );
};

Ribbon.defaultProps = {
  className: '',
  mountTo: 'right'
};

Ribbon.propTypes = {
  /** Class name */
  className: PropTypes.node,
  /** Text to display in ribbon */
  children: PropTypes.node,
  /** Side to mount ribbon */
  mountTo: PropTypes.oneOf(['right', 'left'])
};

Ribbon.legacyName = 'ux-ribbon-singtel';
export default Ribbon;
