import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Equalizer from 'react-equalizer';

import { isNarrowViewport, noop } from '../../helpers';

import Grid from '../Grid';
import Row from '../Row';
import Column from '../Column';
import DropDown from '../DropDown';
import CatalogueCard from '../CatalogueCard';
import Panel from '../Panel';
import SC from './styles';

class CatalogueList extends Component {
  constructor(props) {
    super(props);

    this.refCards = [];

    this.state = {
      showQuantity: props.showQuantity,
      collection: [],
      filters: [],
      sizeCollection: []
    };
  }

  handleFilterChange(fieldName, item) {
    let filters = this.state.filters.slice();

    if (item.value !== '') {
      filters[fieldName] = item.value;
    } else {
      delete filters[fieldName];
    }

    this.setState({ filters: filters, showQuantity: this.props.showQuantity });
    this.props.onFilter(filters);
  }

  handleSortChange(item) {
    this.props.onSort(item.value);
  }

  handleShowMore(e) {
    e.preventDefault();
    this.setState({
      showQuantity: this.state.showQuantity + this.props.showQuantity
    });
  }

  // Used by Equalizer to determine which child components to equalise
  // CatalogueCard has a refTitle ref that we want to equalise height
  getCardTitleNodes() {
    return this.refCards.filter(card => card).map(card => card.refTitle);
  }

  getCardDescriptionNodes() {
    return this.refCards.filter(card => card).map(card => card.refDescription);
  }

  enableEqualizer = () => this.props.enableEqualizer && !isNarrowViewport();

  render() {
    const {
      className,
      ctaType,
      headerText,
      children,
      filterCriteriaText,
      filterLists,
      showQuantityIncrement,
      sortList,
      sortByText,
      collection,
      showHeader,
      columnSize,
      titleStyle,
      descriptionStyle
    } = this.props;

    const filteredCards = collection
      .slice(0, this.state.showQuantity)
      .map((item, key) => {
        return (
          <Column
            sm={columnSize.sm}
            md={columnSize.md}
            lg={columnSize.lg}
            key={key}
          >
            <Equalizer
              enabled={this.enableEqualizer}
              nodes={this.getCardTitleNodes.bind(this)}
            >
              <Equalizer
                enabled={this.enableEqualizer}
                nodes={this.getCardDescriptionNodes.bind(this)}
              >
                <CatalogueCard
                  index={key}
                  {...item}
                  ref={node => (this.refCards[key] = node)}
                  ctaType={ctaType}
                  ctaSecondary={ctaType === 'button'}
                  titleStyle={titleStyle}
                  descriptionStyle={descriptionStyle}
                />
              </Equalizer>
            </Equalizer>
          </Column>
        );
      });

    const ShowMoreButton = () => {
      if (
        collection.length > showQuantityIncrement &&
        collection.length > this.state.showQuantity
      ) {
        return (
          <SC.ShowMoreRow center="sm">
            <Column sm={12}>
              <SC.ShowMoreButton
                secondary
                onClick={this.handleShowMore.bind(this)}
              >
                Show More
              </SC.ShowMoreButton>
            </Column>
          </SC.ShowMoreRow>
        );
      } else return false;
    };

    return (
      <Grid className={className}>
        {showHeader && (
          <Row>
            <Column>
              <Panel>
                <Row>
                  <Column sm={12}>
                    <SC.Heading>{headerText}</SC.Heading>
                  </Column>
                  <Column sm={12}>
                    <SC.Description>{children}</SC.Description>
                  </Column>
                </Row>
                <Row>
                  <Column lg={9} md={9} sm={12}>
                    {filterLists && (
                      <SC.FilterLabel>{filterCriteriaText}</SC.FilterLabel>
                    )}
                    <Row>
                      {filterLists.map((item, key) => {
                        return (
                          <SC.FilterColumn sm={12} md={4} lg={4} key={key}>
                            <DropDown
                              placeholder={item.filterName}
                              items={item.filterOptions}
                              selectedIndex={0}
                              onChange={this.handleFilterChange.bind(
                                this,
                                item.filterName
                              )}
                            />
                          </SC.FilterColumn>
                        );
                      })}
                    </Row>
                  </Column>

                  {sortList && (
                    <Column sm={12} md={3} lg={3}>
                      <SC.FilterLabel>{sortByText}</SC.FilterLabel>
                      <Row>
                        <SC.FilterColumn sm={12} lg={12}>
                          <DropDown
                            items={sortList}
                            selectedIndex={0}
                            onChange={this.handleSortChange.bind(this)}
                          />
                        </SC.FilterColumn>
                      </Row>
                    </Column>
                  )}
                </Row>
              </Panel>
            </Column>
          </Row>
        )}
        <Row>{filteredCards}</Row>
        <ShowMoreButton />
      </Grid>
    );
  }
}

CatalogueList.defaultProps = {
  filterLists: [],
  collection: [],
  showQuantity: 9,
  showQuantityIncrement: 9,
  onSort: noop,
  onFilter: noop,
  onPostHeadingTextClick: noop,
  showHeader: true,
  columnSize: { sm: 12, md: 6, lg: 4 },
  enableEqualizer: true
};

CatalogueList.propTypes = {
  /** Additional class name */
  className: PropTypes.string,
  /** Collection of Items */
  collection: PropTypes.array,
  /** Adjust card column size for different viewports */
  columnSize: PropTypes.shape({
    sm: PropTypes.number,
    md: PropTypes.number,
    lg: PropTypes.number
  }),
  /** CTA type */
  ctaType: PropTypes.string,
  /** Description text as children */
  children: PropTypes.node,
  /** Filter criteria label */
  filterCriteriaText: PropTypes.string,
  /** List of filters to display in dropdowns (takes the shape of the Dropdown items props) */
  filterLists: PropTypes.arrayOf(
    PropTypes.shape({
      filterName: PropTypes.string.isRequired,
      filterOptions: PropTypes.arrayOf(
        PropTypes.shape({
          value: PropTypes.string.isRequired,
          text: PropTypes.string.isRequired
        })
      ).isRequired
    })
  ).isRequired,
  /* Spread style into title element of CatalogueCard */
  titleStyle: PropTypes.object,
  /* Spread style into description element of CatalogueCard */
  descriptionStyle: PropTypes.object,
  /* Enable/Disable equalizer */
  enableEqualizer: PropTypes.bool,
  /** Header text */
  headerText: PropTypes.string,
  /** Show/hide header */
  showHeader: PropTypes.bool,
  /** Number of cards to show on load */
  showQuantity: PropTypes.number,
  /** Number of cards to increment on show more */
  showQuantityIncrement: PropTypes.number,
  /** Sort by label */
  sortByText: PropTypes.string,
  /** On Sort handler */
  onSort: PropTypes.func,
  /** On Filter handler */
  onFilter: PropTypes.func
};

export default CatalogueList;
