import React from 'react';
import PropTypes from 'prop-types';
import { useAppContext } from '../AppContext';
import loadable from '@loadable/component';
const MegaNavPhone = loadable(() => import('./MegaNavPhone'));
const MegaNavDesktop = loadable(() => import('./MegaNavDesktop'));

import { LargeAndUp, LargeDown } from '../../theme/Breakpoints';

const MegaNav = ({ sections = [] }) => {
  const [{ showMegaNav }] = [true];
  return (
    <React.Fragment>
      <LargeDown>
        <MegaNavPhone sections={sections} />
      </LargeDown>
      <LargeAndUp>
        <MegaNavDesktop sections={sections} />
      </LargeAndUp>
    </React.Fragment>
  );
};

MegaNav.propTypes = {
  sections: PropTypes.arrayOf(PropTypes.object)
};

export default MegaNav;
