import React, { Component } from 'react';
import PropTypes from 'prop-types';
import RadioButton from '../RadioButton';
import SC from './styles';

class RadioButtonGroup extends Component {
  state = {
    data: []
  };

  handleChange = checkedValue => {
    this.props.onChange && this.props.onChange(checkedValue);
  };

  componentDidMount() {
    const radioButtonData = this.checkType(this.props.data);
    this.setState({
      data: radioButtonData
    });
  }

  checkType(obj) {
    let newArray = [];
    if (obj instanceof Array) {
      newArray = obj;
    } else {
      newArray = Object.keys(obj).map(value => {
        return {
          label: obj[value],
          value: value
        };
      });
    }
    return newArray;
  }

  renderRadioButtons = () => {
    const { name, form, disabled, value } = this.props;
    const newData = this.checkType(this.state.data);

    return newData.map((buttonValue, i) => {
      const buttonLabel = buttonValue.label,
        buttonDesc = buttonValue.description,
        buttonVal = buttonValue.value;
      return (
        <RadioButton
          key={i}
          name={name}
          form={form}
          label={buttonLabel}
          value={buttonVal}
          disabled={disabled}
          checked={value === buttonVal}
          onChange={this.handleChange}
        >
          {buttonDesc}
        </RadioButton>
      );
    });
  };

  render() {
    const { inline } = this.props;
    return (
      <SC.RadioButtonGroup inline={inline} between="sm">
        {this.renderRadioButtons()}
      </SC.RadioButtonGroup>
    );
  }
}

RadioButtonGroup.defaultProps = {
  className: '',
  form: '',
  name: '',
  data: {},
  disabled: false,
  value: ''
};

RadioButtonGroup.propTypes = {
  /** Class name */
  className: PropTypes.node,
  /** Set the form */
  form: PropTypes.string,
  /** Name of the group of radio buttons it belongs to */
  name: PropTypes.string,
  /** RadioButton data are in the form of either Object : <b>{ 'value' : 'label' }</b> or Array to support description <b>[{ 'value': 'option1', 'label' : 'Option 1', description: 'This is a sample description.'}]</b> */
  data: PropTypes.oneOfType([
    PropTypes.arrayOf(
      PropTypes.shape({
        text: PropTypes.string,
        value: PropTypes.string,
        description: PropTypes.string
      })
    ),
    PropTypes.object
  ]),
  /** RadioButtons can be arranged inline or vertical */
  inline: PropTypes.bool,
  /** RadioButtons can be disabled */
  disabled: PropTypes.bool,
  /** Current value of selected button */
  value: PropTypes.string,
  /** onChange handler. Returns currently checked value */
  onChange: PropTypes.func
};

RadioButtonGroup.legacyName = 'ux-radio-button-group';

export default RadioButtonGroup;
