import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SC from './styles';
import IconTick from '../../assets/svgs/tick.svg';
import Theme from '../../theme';
import { noop } from '../../helpers';

class CheckBox extends Component {
  constructor(props) {
    super(props);

    this.state = {
      checked: props.checked
    };
  }

  render() {
    return (
      <SC.CheckBoxWrapper onClick={this.handleChange.bind(this)}>
        <input
          type="checkbox"
          form={this.props.form}
          id={this.props.id}
          name={this.props.name}
          onChange={this.handleChange.bind(this)}
          onFocus={this.props.onFocus}
          onBlur={this.props.onBlur}
          disabled={this.props.disabled}
          checked={this.state.checked}
        />
        <span>
          {this.state.checked && (
            <IconTick
              size={22}
              fill={
                !this.props.disabled
                  ? Theme.colours.linkblue
                  : Theme.colours.grey_c6
              }
            />
          )}
        </span>
      </SC.CheckBoxWrapper>
    );
  }

  handleChange(e) {
    if (!this.props.disabled) {
      this.setState({
        checked: e.target.checked || !this.state.checked
      });
      this.props.onChange(e);
    }
  }
}

CheckBox.defaultProps = {
  disabled: false,
  checked: false,
  onChange: noop,
  onFocus: noop,
  onBlur: noop
};

CheckBox.propTypes = {
  /** Anchor text */
  children: PropTypes.node,
  /** Extra class to apply to element */
  className: PropTypes.string,
  /** ID of input */
  id: PropTypes.string,
  /** Set the form */
  form: PropTypes.string,
  /** Disable the element */
  disabled: PropTypes.bool,
  /** Name of input field */
  name: PropTypes.string,
  /** Value binded to this element */
  value: PropTypes.string,
  /** On blur function */
  onBlur: PropTypes.func,
  /** On change function */
  onChange: PropTypes.func,
  /** On click function */
  onClick: PropTypes.func,
  /** On focus function */
  onFocus: PropTypes.func
};

CheckBox.legacyName = 'ux-checkbox';
export default CheckBox;
