import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SC from './styles';

class ToggleSwitch extends Component {
  constructor(props) {
    super(props);

    this.state = {
      checked: props.switchButtonValue,
      switchButtonValue: props.switchButtonValue
    };

    this.handleToggleChange = this.handleToggleChange.bind(this);
  }

  handleToggleChange = e => {
    if (e.target.type == 'checkbox' && !this.props.disabled) {
      this.setState({
        checked: e.target.checked,
        switchButtonValue: e.target.checked
      });
      this.props.onToggleChange(e.target.checked);
    }
  };

  render() {
    return (
      <SC.ToggleSwitch id={this.props.switchButtonId}>
        <SC.Label onClick={this.handleToggleChange}>
          <SC.CheckBox
            type="checkbox"
            value={this.state.checked}
            defaultChecked={this.state.checked}
            disabled={this.props.disabled}
          />
          {(this.props.textDisabled || this.props.textEnabled) && (
            <SC.P active={this.state.checked}>
              {this.state.checked
                ? this.props.textEnabled
                : this.props.textDisabled}
            </SC.P>
          )}
          <SC.Span active={this.state.checked} disabled={this.props.disabled} />
        </SC.Label>
      </SC.ToggleSwitch>
    );
  }
}

ToggleSwitch.defaultProps = {
  textEnabled: '',
  textDisabled: '',
  switchButtonValue: false,
  switchButtonId: '',
  disabled: false,
  onToggleChange: function() {}
};

ToggleSwitch.propTypes = {
  /** Toggle switch id */
  switchButtonId: PropTypes.string,
  /** Default toggle switch value */
  switchButtonValue: PropTypes.bool,
  /** Optional: If toggle is on, show text */
  textEnabled: PropTypes.string,
  /** Optional: If toggle is off, show text */
  textDisabled: PropTypes.string,
  /** OnChange function  */
  onToggleChange: PropTypes.func,
  /** Disable switching of the toggle */
  disabled: PropTypes.bool
};

ToggleSwitch.legacyName = ['ux-button-switch'];
export default ToggleSwitch;
