import styled, { css } from 'styled-components';

import Theme from '../../theme';

export default {
  UsageBar: styled.div`
    padding: 0.5rem 0;
  `,
  UsageDetails: styled.div`
    display: block;
    padding-bottom: 0.25rem;
    font-family: ${Theme.fonts.families.AvenirBook};

    > strong {
      font-family: ${Theme.fonts.families.AvenirHeavy};
    }
  `,
  BarOuter: styled.div`
    height: 1rem;
    width: 100%;
    border-radius: 1rem;
    background-color: ${Theme.colours.grey_e5};
    overflow: hidden;
  `,
  BarInner: styled.div`
    height: 100%;
    ${props => {
      if (props.secondaryColour) {
        return css`
          width: ${props.size}%;
          background: repeating-linear-gradient(
            -45deg,
            ${props.primaryColour},
            ${props.primaryColour} 20px,
            ${props.secondaryColour} 20px,
            ${props.secondaryColour} 40px
          );
        `;
      } else {
        return css`
          width: ${props.size}%;
          background: ${props.primaryColour};
          transition: width 500ms;
        `;
      }
    }};
  `,
  ExceededAmount: styled.span`
    color: ${Theme.colours.rederror};
  `
};
