import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SC from './styles';
import { noop } from '../../helpers';

class RadioButton extends Component {
  handleChange = () => {
    if (!this.props.disabled) {
      this.props.onChange(this.props.value);
    }
  };

  render() {
    const {
      form,
      id,
      name,
      label,
      checked,
      value,
      disabled,
      children
    } = this.props;

    return (
      <SC.RadioButton>
        <SC.Hoverable disabled={disabled}>
          <SC.RadioButtonContainer>
            <SC.RadioButtonWrapper
              onClick={this.handleChange}
              disabled={disabled}
            >
              <input
                type="radio"
                form={form}
                id={id}
                name={name}
                value={value}
                checked={this.props.checked}
                onChange={this.handleChange}
                disabled={disabled}
              />
              <span>
                <SC.Fill disabled={disabled} />
              </span>
            </SC.RadioButtonWrapper>
          </SC.RadioButtonContainer>

          <SC.TextContainer disabled={disabled}>
            <SC.Label
              onClick={this.handleChange}
              checked={checked}
              disabled={disabled}
              isBold={children ? true : false}
            >
              {label}
            </SC.Label>
            {children && <SC.Description>{children}</SC.Description>}
          </SC.TextContainer>
        </SC.Hoverable>
      </SC.RadioButton>
    );
  }
}

RadioButton.defaultProps = {
  className: '',
  name: '',
  value: '',
  label: '',
  disabled: false,
  checked: false,
  id: '',
  form: '',
  onChange: noop,
  children: ''
};

RadioButton.propTypes = {
  /** Class name */
  className: PropTypes.node,
  /** ID of input */
  id: PropTypes.string,
  /** Set the form */
  form: PropTypes.string,
  /** Label */
  label: PropTypes.string,
  /** Value of current selection */
  value: PropTypes.string,
  /** RadioButton can be checked */
  checked: PropTypes.bool,
  /** RadioButton can be disabled */
  disabled: PropTypes.bool,
  /** Name of the group of radio buttons it belongs to*/
  name: PropTypes.string,
  /** onChange function that returns the value of the selected radio button*/
  onChange: PropTypes.func,
  /** Add description for the label */
  children: PropTypes.node
};

RadioButton.legacyName = 'ux-radio-button';

export default RadioButton;
